<template>
  <div class="monitor">
    <div class="head"><div>目标网站监控</div></div>
    <div class="condition">
      目标网站：
      <el-select size="small" v-model="web_name">
        <el-option v-for="(item, index) in target_web_list" :key="index" :value="item.web_name" :label="item.web_name">{{item.web_name}}</el-option>
      </el-select>
      <span class="margin"></span>
      当前状态：
      <el-select size="small" v-model="status">
        <el-option value="正常" label="正常">正常</el-option>
        <el-option value="断开" label="断开">断开</el-option>
      </el-select>
      <span class="margin"></span>
      网站链接：<el-input size="small" style="width: 200px;" placeholder="请输入关键词搜索" v-model="url"></el-input><span class="margin"></span>
      <el-button size="small" @click="fetchData(1)">搜索</el-button>
      <el-button size="small" @click="web_name = '', status = '', url = '', fetchData(1)">清空</el-button>
    </div>
    <el-table
      :data="tableData">
      <el-table-column
        prop="web_name"
        label="目标网站"
        sortable>
      </el-table-column>
      <el-table-column
        prop="web_url"
        label="网站链接"
        sortable>
      </el-table-column>
      <el-table-column
        prop="web_status"
        label="当前状态"
        sortable>
      </el-table-column>
      <el-table-column
        prop="monitor_time"
        label="监控时间"
        sortable>
      </el-table-column>
      <el-table-column
        prop="disconnected_num"
        label="断开次数"
        sortable>
      </el-table-column>
      <el-table-column
        prop="disconnected_time"
        label="上次断开时间"
        sortable>
      </el-table-column>
      <el-table-column
        prop="job_name"
        label="查看">
        <template slot-scope="scope">
          <router-link :to="'/monitor/log/' + scope.row.web_id">
            <el-button size="small" type="text">日志</el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <div class="text-center">
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="fetchData"></el-pagination>
    </div>
  </div>
</template>

<script>
import axios from '@/config/http'
export default {
  name: 'monitor',
  data () {
    return {
      tableData: [],
      target_web_list: [],
      web_name: '',
      status: '',
      url: '',
      total: 1
    }
  },
  methods: {
    fetchData (page = 1) {
      axios.get(`/api/web_monitor/${page}`, {
        params: {
          web_name: this.web_name,
          status: this.status,
          url: this.url
        }
      }).then(res => {
        this.tableData = res.data.rsts
        this.target_web_list = res.data.target_web_list
        this.total = res.data.target_web_num
      })
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>

<style scoped>
.monitor .head{
  border-bottom: 1px solid #ccc;
  padding-bottom: 1px;
  height: 30px;
  line-height: 30px;
}
.monitor .head div{
  display: inline-block;
  padding: 0 5px;
  border-bottom: 3px solid #333;
}
.monitor .condition{
  padding: 20px;
}
.monitor .margin{
  margin: 0 10px;
}
</style>



