<template>
  <div style="position: relative;height:100%;">
    <div class="login">
      <h1>知音晓视系统采集管理</h1>
      <div class="box">
        <div class="head">账户登录</div>
        <div class="form">
          <div class="form-item">
            <el-input placeholder="请输入用户名" v-model="username">
              <span slot="prepend"><i class="el-icon-info"></i></span>
            </el-input>
          </div>
          <div class="form-item">
            <el-input placeholder="请输入密码" type="password" v-model="password">
              <span slot="prepend"><i class="el-icon-info"></i></span>
            </el-input>
          </div>
          <div class="form-item">
            <el-button type="primary" @click="submit">立即登录</el-button>
          </div>
        </div>
      </div>
      <div class="about">Copyright © 2018 这里是单位名称</div>
    </div>
  </div>
</template>

<script>
import axios from '@/config/http'
import qs from 'qs'
import bus from '@/bus/bus'
export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    submit () {
      axios.post('/api/user/login', qs.stringify({
        username_or_token: this.username,
        password: this.password
      })).then(res => {
        if (res.data.token) {
          const token = res.data.token
          window.localStorage.setItem('token', token)
          window.localStorage.setItem('user_name', res.data.user_name)
          bus.$emit('login')
          this.$router.replace('/')
        }
      })
    }
  }
}
</script>

<style scoped>
.login{
  position: absolute;
  top: 50%;left: 50%;
  transform: translateY(-215px) translateX(-200px);
  text-align: center;
  /* margin:  80px auto 0; */
  height: 430px;
  width: 400px;
}
h1{
  font-size: 30px;
}
.box{
  margin: 20px 0;
  height: 330px;
  border: 1px solid #ccc;
}
.box .head{
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  font-size: 16px;
  font-weight: 700;
}
.form{
  width: 330px;
  margin: 35px auto 0;
}
.form-item{
  margin-bottom: 35px;
}
.el-button{
  width: 100%;
}
</style>



