<script>
import axios from '@/config/http'
import echarts from 'echarts'
export default {
  name: 'result',
  data () {
    return {
      showDetail: false,
      detailData: '',
      tabs: '1',
      name: '',
      status: '',
      state: '',
      tableData1: [],
      total1: 1,
      selects1: [],
      job_id1: '',
      time1: [],
      running_status: '',
      tableData2: [],
      total2: 1,
      selects2: [],
      job_id2: '',
      time2: [],
      web_list: [],
      title: '',
      site_name: '',
      job_name_list: [],
      time3: [],
      webTime: [],
      jobTime: [],
      head_result: [],
      videos_num_by_job: [],
      videos_num_by_web: [],
      videos_increase: [],
      videos_increase_by_job: [],
      videos_increase_by_web: [],
      videos_increase_by_job_model: '',
      videos_increase_by_job_options: [],
      videos_increase_by_web_options: [],
      videos_increase_by_web_model: '',
      lineBy: 'day'
    }
  },
  methods: {
    tabClick (tab) {
      this['getData' + tab.name]()
    },
    videoDetail (id) {
      this.showDetail = true
      axios.get(`/api/joblist/video_detail/${id}`).then(res => {
        if (res.data.code === 200) {
          this.detailData = res.data.rst
        }
      })
    },
    collectionDetail (date, id) {
      this.tabs = '2'
      this.time2 = [date, date]
      this.job_id2 = id
      this.getData2(1)
    },
    getData1 (page = 1) {
      axios.get(`/api/job_executions/${page}`, {
        params: {
          start_date: this.time1[0],
          end_date: this.time1[1],
          job_id: this.job_id1,
          running_status: this.running_status
        }
      }).then(res => {
        this.tableData1 = res.data.rsts
        this.total1 = res.data.job_excution_num
        this.selects1 = res.data.job_name_list
      })
    },
    getData2 (page = 1) {
      axios.get(`/api/joblist/videos/${page}`, {
        params: {
          start_date: this.time2[0],
          end_date: this.time2[1],
          title: this.title,
          site_name: this.site_name,
          job_id: this.job_id2
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.tableData2 = res.data.rst.rsts
          this.total2 = res.data.rst.video_num
          this.job_name_list = res.data.rst.job_name_list
          this.web_list = res.data.rst.web_list
        }
      })
    },
    getData3 () {
      this.getChart1()
      this.getChart2()
      this.getChart3()
    },
    getChart1 (time = []) {
      axios.get('/api/spider_result/total/by_job', {
        params: {
          start_date: time[0],
          end_date: time[1]
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.head_result = res.data.head_result
          this.videos_num_by_job = res.data.videos_num_by_job
          this.renderBar(res.data.videos_num_by_job.map(item => { return {name: item.job_name, value: item.videos_num} }), 'bar1')
          this.renderPie(res.data.videos_num_by_job.map(item => { return {name: item.job_name, value: item.videos_num} }), 'pie1')
        }
      })
    },
    getChart2 (time = []) {
      axios.get('/api/spider_result/total/by_web', {
        params: {
          start_date: time[0],
          end_date: time[1]
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.videos_num_by_web = res.data.videos_num_by_web
          this.renderBar2(res.data.videos_num_by_web, 'bar2')
          this.renderPie2(res.data.videos_num_by_web, 'pie2')
        }
      })
    },
    getChart3 (time = [], by = 'day') {
      axios.get('/api/spider_result/total/new_increase', {
        params: {
          start_date: time[0],
          end_date: time[1]
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.videos_increase = res.data.videos_increase
          this.videos_increase_by_job = res.data.videos_increase_by_job
          this.videos_increase_by_job_options = this.videos_increase_by_job.map(item => item.job_name)
          this.videos_increase_by_web = res.data.videos_increase_by_web
          this.videos_increase_by_web_options = this.videos_increase_by_web.map(item => item.web_name)
          this.renderLineForData('day')
        }
      })
    },
    renderLineForData (by) {
      this.renderLine(
        'line1',
        [],
        this.videos_increase[0]['videos_increase_by_' + by].map(i => i.date),
        [
          {
            data: this.videos_increase[0]['videos_increase_by_' + by].map(i => i.count),
            type: 'line'
          }
        ]
      )
      this.renderLine(
        'line2',
        this.videos_increase_by_job.map(i => i.job_name),
        this.videos_increase_by_job[0]['videos_increase_by_job_' + by].map(i => i.date),
        this.videos_increase_by_job.map(item => {
          return {
            name: item.job_name,
            type: 'line',
            data: item['videos_increase_by_job_' + by].map(i => i.count)
          }
        })
      )
      this.renderLine(
        'line3',
        this.videos_increase_by_web.map(i => i.web_name),
        this.videos_increase_by_web[0]['videos_increase_by_web_' + by].map(i => i.date),
        this.videos_increase_by_web.map(item => {
          return {
            name: item.web_name,
            type: 'line',
            data: item['videos_increase_by_web_' + by].map(i => i.count)
          }
        })
      )
    },
    job_model_change (index) {
      this.renderLine(
        'line2',
        [this.videos_increase_by_job[index]].map(i => i.job_name),
        this.videos_increase_by_job[index]['videos_increase_by_job_' + this.lineBy].map(i => i.date),
        [this.videos_increase_by_job[index]].map(item => {
          return {
            name: item.job_name,
            type: 'line',
            data: item['videos_increase_by_job_' + this.lineBy].map(i => i.count)
          }
        })
      )
    },
    web_model_change (index) {
      this.renderLine(
        'line3',
        [this.videos_increase_by_web[index]].map(i => i.web_name),
        this.videos_increase_by_web[index]['videos_increase_by_web_' + this.lineBy].map(i => i.date),
        [this.videos_increase_by_web[index]].map(item => {
          return {
            name: item.web_name,
            type: 'line',
            data: item['videos_increase_by_web_' + this.lineBy].map(i => i.count)
          }
        })
      )
    },
    renderBar (data, id) {
      echarts.init(document.getElementById(id)).setOption({
        color: ['#3398DB'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: data.map(item => item.name),
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            type: 'bar',
            barWidth: '60%',
            data: data.map(item => item.value)
          }
        ]
      })
    },
    renderPie (data, id) {
      echarts.init(document.getElementById(id)).setOption({
        color: ['#37A2DA', '#32C5E9', '#67E0E3', '#9FE6B8', '#FFDB5C', '#ff9f7f', '#fb7293', '#E062AE', '#E690D1', '#e7bcf3', '#9d96f5', '#8378EA', '#96BFFF'],
        tooltip: {
          trigger: 'item',
          formatter: '{b} : {d}%'
        },
        legend: {
          orient: 'vertical',
          left: 'right',
          data: data.map(item => item.name)
        },
        series: [
          {
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: data,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
    },
    renderBar2 (data, id) {
      echarts.init(document.getElementById(id)).setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['关键词采集', '板块采集']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: data.map(item => item.web_name)
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '关键词采集',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: data.map(item => item.videos_num_by_keywords)
          },
          {
            name: '板块采集',
            type: 'bar',
            stack: '总量',
            label: {
              normal: {
                show: true,
                position: 'insideRight'
              }
            },
            data: data.map(item => item.videos_num_by_plate)
          }
        ]
      })
    },
    renderPie2 (data, id) {
      echarts.init(document.getElementById(id)).setOption({
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: data.map(item => item.web_name)
        },
        series: [
          {
            name: '关键词采集',
            type: 'pie',
            radius: ['40%', '55%'],
            label: {
              normal: {
                position: 'inner'
              }
            },
            data: data.map(item => {
              return {
                name: item.web_name,
                value: item.videos_num_by_keywords
              }
            })
          },
          {
            name: '板块采集',
            type: 'pie',
            radius: [0, '30%'],
            label: {
              normal: {
                position: 'inner'
              }
            },
            data: data.map(item => {
              return {
                name: item.web_name,
                value: item.videos_num_by_plate
              }
            })
          }
        ]
      })
    },
    renderLine (id, legend, xAxis, series) {
      echarts.init(document.getElementById(id)).setOption({
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: legend
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: xAxis
        },
        yAxis: {
          type: 'value'
        },
        series: series
      }, true)
    }
  },
  mounted () {
    if (this.$route.query.status) {
      this.job_id1 = Number(this.$route.query.status)
      this.getData1()
    }
    if (this.$route.query.result) {
      this.tabs = '2'
      this.job_id2 = Number(this.$route.query.result)
      this.getData2()
      return
    }
    this.getData1()
  }
}
</script>

<style scoped>
.condition{
  margin-right: 30px;
  line-height:50px;
  float: left;
}
.condition .label{
  display: inline-block;
  width: 75px;
}
.statistics-box{
  padding-top: 20px;
  height:80px;
  background: #f2f2f2;
  margin-bottom: 20px;
}
.statistics-col{
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-left: 1px solid #ccc;
}
.statistics-col:first-of-type{
  border-left: none;
}
.statistics-item{
  display: inline-block;
  height: 60px;
  min-width: 220px;
}
.statistics-item .tag{
  height: 60px;
  line-height: 60px;
  width: 60px;
  text-align: center;
  background: #999;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  border-radius: 10px;
  margin-right: 20px;
}
.statistics-item .sum{
  line-height: 30px;
}
.collection-detail{
  color: #333;
  cursor: pointer;
  font-weight: bold;
}
.detail .content{
  position: relative;
  padding: 20px;
  border-bottom: 1px solid #ccc;
}
.detail .result{
  position: absolute;
  top: 5px;
  right: 0;
}
.detail .content li{
  line-height: 40px;
}
.detail .content .left{
  width: 100px;
}
.border-box{
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #bcbcbc;
}
.border-box .title-box{
  margin-bottom: 10px;
}
.border-box .title-text{
  color: #333;
  font-size: 18px;
  font-weight: bold;
}
.charts-box{
  height: 250px;
}
</style>



