<template>
  <div id="app">
    <el-container style="height: 100%;">
      <el-header>
        <div class="clearfix header">
          <router-link to="/" tag="a" class="pull-left logo">
            知音晓视系统采集管理
          </router-link>
          <div class="pull-right app-tools">
            <template v-if="isLogin">
              <el-button type="text" size="small">注册</el-button>
              <span class="line">|</span>
              <el-button type="text" size="small">登录</el-button>
            </template>
            <template v-else>
              <el-button type="text" size="small">您好，{{user_name}}</el-button>
              <span class="line">|</span>
              <el-button type="text" size="small" @click="$router.push('/login')">退出</el-button>
            </template>
          </div>
        </div>
      </el-header>
      <el-container style="height: 100%;">
        <el-aside width="200px">
          <el-menu
            router
            :default-active="'/' + $route.path.split('/')[1]">
            <!-- <el-menu-item index="/index">
              <i class="el-icon-menu"></i>
              <span slot="title">首页</span>
            </el-menu-item> -->
            <el-menu-item index="/task">
              <i class="el-icon-setting"></i>
              <span slot="title">任务列表</span>
            </el-menu-item>
            <el-menu-item index="/result">
              <i class="el-icon-document"></i>
              <span slot="title">采集结果</span>
            </el-menu-item>
            <el-menu-item index="/add">
              <i class="el-icon-setting"></i>
              <span slot="title">新增任务</span>
            </el-menu-item>
            <el-menu-item index="/monitor">
              <i class="el-icon-setting"></i>
              <span slot="title">目标网站监控</span>
            </el-menu-item>
          </el-menu>
        </el-aside>

        <el-main>
          <router-view/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import bus from '@/bus/bus'
export default {
  name: 'App',
  data () {
    return {
      isLogin: false,
      user_name: localStorage.getItem('user_name')
      // isLogin: true
    }
  },
  mounted () {
    bus.$on('login', () => {
      this.$message.success('登录成功')
    })
  }
}
</script>

<style scoped>
#app{
  height: 100%;
}
.header{
  font-size: 20px;
  font-weight: 700;
}
.logo{
  display: block;
  text-decoration: none;
  color: #fff !important;
}
.app-tools{
  font-size: 14px;
  color: #fff;
}
.line{
  margin: 0 10px;
}
</style>

<style>
.app-tools .el-button--text{
  color: #fff;
}
.el-header {
  background-color: #333;
  line-height: 60px;
}
.el-aside {
  height: 100%;
  color: #333;
}
.el-menu-item.is-active{
  /* background-color: #ecf5fe; */
  background-color: #ccc;
  color: #333;
  border-right:1px solid #999;
}
.el-menu {
  background-color: #E9EEF3;
  height: 100%;
}
</style>



