<template>
  <div class="monitor-log">
    <div class="head clearfix">
      <div class="pull-left head-left">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/monitor' }">目标网站监控</el-breadcrumb-item>
          <el-breadcrumb-item>日志</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="pull-right">
        <router-link to="/monitor">
          <el-button size="small">返回列表</el-button>
        </router-link>
      </div>
    </div>
    <el-table
      :data="tableData">
      <el-table-column
        prop="monitor_date"
        label="获取时间"
        sortable>
      </el-table-column>
      <el-table-column
        prop="web_name"
        label="目标网站"
        sortable>
      </el-table-column>
      <el-table-column
        prop="web_url"
        label="网站链接"
        sortable>
      </el-table-column>
      <el-table-column
        prop="web_status"
        label="监控状态"
        sortable>
      </el-table-column>
    </el-table>
    <div class="text-center">
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="fetchData"></el-pagination>
    </div>
  </div>
</template>

<script>
import axios from '@/config/http'
export default {
  name: 'monitor',
  data () {
    return {
      id: this.$route.params.id,
      tableData: [],
      total: 1
    }
  },
  methods: {
    fetchData (page = 1) {
      axios.get(`/api/web_monitor/${this.id}/${page}`).then(res => {
        this.tableData = res.data.target_web_monitor_log
        this.total = res.data.total_log_num
      })
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>

<style scoped>
.head{
  border-bottom: 1px solid #ccc;
  height: 36px;
  line-height: 36px;
  padding-bottom: 1px;
}
.head-left{
  margin-top: 13px;
  padding-bottom: 8px;
  border-bottom: 3px solid #333;
}
.monitor-log .breadcrumb{
  text-decoration: none;
  font-weight: bold;
  color: #333;
}
.monitor-log .margin{
  margin: 0 10px;
}
</style>



