<template>
  <div class="add">
    <div class="head"><div>编辑任务</div></div>
    <div style="padding:20px;">
      <el-form label-width="120px" size="small">
        <el-form-item label="任务名称：">
          <el-input placeholder="请输入任务名称" style="width: 300px;" v-model.trim="form.job_name"></el-input><span class="tips">20个字内</span>
        </el-form-item>
        <el-form-item label="采集形式：">
          <el-select style="width: 200px;" v-model="form.spider_type">
            <el-option value="关键词采集" label="关键词采集">关键词采集</el-option>
            <el-option value="板块采集" label="板块采集">板块采集</el-option>
          </el-select>
        </el-form-item>
        <div v-show="form.spider_type === '关键词采集'">
          <el-form-item label="目标网站：">
            <el-select style="width: 200px;" v-model="form.project_id">
              <el-option v-for="item in target_webs1" :key="item.project_id" :value="item.project_id" :label="item.project_name">{{item.project_name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="采集内容：">
            <el-tag closable type="info" v-for="(item, index) in words" :key="index" @close="handleClose" :disable-transitions="false">{{item}}</el-tag>
            <el-input
              v-if="inputVisible"
              v-model="inputValue"
              ref="saveTagInput"
              size="small"
              style="width:100px"
              @keyup.enter.native="handleInputConfirm"
              @blur="handleInputConfirm"
            ></el-input>
            <el-button v-else icon="el-icon-plus" size="small" @click="showInput">新关键词</el-button>
          </el-form-item>
        </div>
        <div v-show="form.spider_type === '板块采集'">
          <el-form-item label="目标网站：">
            <el-select style="width: 200px;" v-model="form.project_id" @change="targetChange">
              <el-option v-for="item in target_webs2" :key="item.project_id" :value="item.project_id" :label="item.project_name">{{item.project_name}}</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="采集内容：">
            <el-select style="width: 200px;" v-model="caijineirong">
              <el-option v-for="item in caijineirongArr" :key="item" :value="item" :label="item">{{item}}</el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="任务运行时间：">
          <el-select style="width: 200px;" v-model="form.run_time">
            <el-option value="长期" label="长期">长期</el-option>
            <el-option value="设定区间" label="设定区间">设定区间</el-option>
          </el-select>
          <el-date-picker
            v-show="form.run_time === '设定区间'"
            v-model="time1"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="form.start_date = time1[0], form.end_date = time1[1]">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="采集频次：">
          <el-input placeholder="请输入数字" style="width: 100px;" v-model="form.spider_freq"></el-input><span class="tips">天采集一次</span>
        </el-form-item>
        <el-form-item label="任务运行次数：">
          <el-select style="width: 200px;" v-model="form.run_type">
            <el-option value="持续运行" label="持续运行">持续运行</el-option>
            <el-option value="运行一次" label="运行一次">运行一次</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="视频上传时间：">
          <el-select style="width: 200px;" v-model="form.upload_time_type">
            <el-option value="任务运行周期内最新" label="任务运行周期内最新">任务运行周期内最新</el-option>
            <el-option value="设定区间" label="设定区间">设定区间</el-option>
          </el-select>
          <el-date-picker
            v-show="form.upload_time_type === '设定区间'"
            v-model="time2"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="form.upload_time_start_date = time2[0], form.upload_time_end_date = time2[1]">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="视频时长：">
          <el-input placeholder="最短时长" style="width: 83px;" v-model="form.video_time_short"></el-input>
          min ~
          <el-input placeholder="最长时长" style="width: 83px;" v-model="form.video_time_long"></el-input>
          min
          <span class="tips">请输入数字</span>
        </el-form-item>
        <el-form-item label="服务器：">
          <el-select style="width: 200px;" v-model="form.daemon">
            <el-option v-for="item in servers" :key="item" :value="item === 'auto' ? '自动分配' : item" :label="item === 'auto' ? '自动分配' : item">{{item === 'auto' ? '自动分配' : item}}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="任务优先级：">
          <el-select style="width: 200px;" v-model="form.pri">
            <el-option value="常规" label="常规">常规</el-option>
            <el-option value="紧急" label="紧急">紧急</el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="text-center">
      <el-button type="primary" @click="submit">确定</el-button>
      <el-button>取消</el-button>
    </div>
  </div>
</template>

<script>
import qs from 'qs'
import axios from '@/config/http'
export default {
  name: 'add',
  data () {
    return {
      id: this.$route.params.id,
      form: {
        job_name: '',
        spider_type: '',
        project_id: '',
        keywords: '',
        run_time: '',
        start_date: '',
        end_date: '',
        spider_freq: '',
        run_type: '',
        upload_time_type: '',
        upload_time_start_date: '',
        upload_time_end_date: '',
        video_time_short: '',
        video_time_long: '',
        daemon: '',
        pri: ''
      },
      target_webs1: [],
      target_webs2: [],
      servers: [],
      mubiaowangzhan: 'YouTube',
      caijineirong: '',
      caijineirongArr: [],
      words: [],
      inputVisible: false,
      inputValue: '',
      time1: [],
      time2: []
    }
  },
  methods: {
    handleClose (tag) {
      this.words.splice(this.words.indexOf(tag), 1)
    },
    showInput () {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleInputConfirm () {
      if (this.inputValue && this.words.indexOf(this.inputValue) < 0) {
        this.words.push(this.inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },
    targetChange (id) {
      this.target_webs2.forEach(item => {
        if (item.project_id === id) {
          this.caijineirongArr = item.plate_name
        }
      })
    },
    fetchData () {
      axios.get(`/api/joblist/${this.id}`).then(res => {
        if (res.data.code === 200) {
          this.form = res.data.rst
        }
      })
    },
    getOptions () {
      axios.get('/api/project/add_jobs').then(res => {
        if (res.data.code === 200) {
          this.servers = res.data.rst.servers
          this.servers.unshift('auto')
          this.target_webs1 = res.data.rst.spider_type[0]['关键词采集'].target_web
          this.target_webs2 = res.data.rst.spider_type[1]['板块采集'].target_web
        }
      }).then(res => {
        axios.get(`/api/joblist/${this.id}`).then(res => {
          if (res.data.code === 200) {
            this.form = res.data.rst
          }
        })
      })
    },
    submit () {
      axios.post('/api/project/add_jobs', qs.stringify(this.form)).then(res => {
        if (res.data.code === 200) {
          this.$message.success(res.data.rst)
          this.form = {
            job_name: '',
            spider_type: '',
            project_id: '',
            keywords: '',
            run_time: '',
            start_date: '',
            end_date: '',
            spider_freq: '',
            run_type: '',
            upload_time_type: '',
            upload_time_start_date: '',
            upload_time_end_date: '',
            video_time_short: '',
            video_time_long: '',
            daemon: '',
            pri: ''
          }
        }
      })
    }
  },
  mounted () {
    this.getOptions()
  },
  watch: {
    words (val) {
      this.form.keywords = val.toString()
    },
    caijineirong (val) {
      this.form.keywords = val
    }
  }
}
</script>

<style scoped>
.head{
  border-bottom: 1px solid #ccc;
  padding-bottom: 1px;
  height: 30px;
  line-height: 30px;
}
.head div{
  display: inline-block;
  padding: 0 5px;
  border-bottom: 3px solid #333;
}
.tips{
  color: #999;
  margin-left: 5px;
}
.add .el-tag{
  margin: 0 3px 3px 0;
}
</style>



