import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/index.vue'
import Index from '@/views/index/index'
import Task from '@/views/task/index'
import TaskDetail from '@/views/task/detail'
import TaskEdit from '@/views/task/edit'
import Result from '@/views/result/index'
import Add from '@/views/add/index'
import Monitor from '@/views/monitor/index'
import MonitorLog from '@/views/monitor/log'
import Login from '@/views/login/index'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '',
      component: Main,
      children: [
        {
          path: '/',
          redirect: '/task'
        },
        {
          path: '/index',
          name: 'Index',
          component: Index,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/task',
          name: 'Task',
          component: Task,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/task/detail/:id',
          name: 'TaskDetail',
          component: TaskDetail,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/task/edit/:id',
          name: 'TaskEdit',
          component: TaskEdit,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/result',
          name: 'Result',
          component: Result,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/add',
          name: 'Add',
          component: Add,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/monitor',
          name: 'Monitor',
          component: Monitor,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/monitor/log/:id',
          name: 'MonitorLog',
          component: MonitorLog,
          meta: {
            requireAuth: true
          }
        }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  let token = window.localStorage.getItem('token')
  if (to.meta.requireAuth) {
    if (token) {
      next()
    } else {
      next({
        path: '/login'
      })
    }
  } else {
    next()
  }
})

export default router



