<template>
  <div class="task-detail">
    <div class="head clearfix">
      <div class="pull-left head-left">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/task' }">任务列表</el-breadcrumb-item>
          <el-breadcrumb-item>任务详情</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="pull-right">
        <router-link to="/task">
          <el-button size="small">返回列表</el-button>
        </router-link>
      </div>
    </div>
    <ul class="content">
      <div class="result">
        <el-button size="small" type="primary" @click="$router.push({name: 'Result', query: {status: id}})">执行情况</el-button>
        <br>
        <br>
        <el-button size="small" type="primary" @click="$router.push({name: 'Result', query: {result: id}})">采集结果</el-button>
      </div>
      <li class="clearfix">
        <div class="left pull-left text-right">任务状态：</div>
        <div class="right pull-left">
          {{data.job_status}}&nbsp;<el-button size="small">暂停任务</el-button>
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">任务名称：</div>
        <div class="right">
          {{data.job_name}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">采集形式：</div>
        <div class="right">
          {{data.spider_type}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">目标网站：</div>
        <div class="right">
          {{data.target_web}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">采集内容：</div>
        <div class="right">
          {{data.spider_content}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">任务运行时间：</div>
        <div class="right">
          {{data.run_time}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">采集频次：</div>
        <div class="right">
          {{data.spider_freq}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">任务运行次数：</div>
        <div class="right">
          {{data.run_times}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">视频上传时间：</div>
        <div class="right">
          {{data.update_time}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">视频时长：</div>
        <div class="right">
          {{data.video_time}}
        </div>
      </li>
      <li class="clearfix">
        <div class="left pull-left text-right">服务器：</div>
        <div class="right">
          {{data.server}}
        </div>
      </li>
    </ul>
    <div class="clearfix foot">
      <div class="pull-left">创建时间：{{data.create_time}}（最后更新于）创建人：{{username}}</div>
      <div class="pull-right"><el-button size="small">编辑任务</el-button></div>
    </div>
  </div>
</template>

<script>
import axios from '@/config/http'
export default {
  name: 'task-detail',
  data () {
    return {
      id: this.$route.params.id,
      data: {
        create_time: '',
        enabled: '',
        job_name: '',
        job_status: '',
        run_time: '',
        run_times: '',
        server: '',
        spider_content: '',
        spider_freq: '',
        spider_type: '',
        target_web: '',
        update_time: '',
        video_time: '',
        video_upload_time: ''
      },
      username: ''
    }
  },
  methods: {
    fetchData () {
      axios.get(`/api/joblist/${this.id}`).then(res => {
        if (res.data.code === 200) {
          this.data = res.data.rst
          this.username = res.data.user_name
        }
      })
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>

<style scoped>
.head{
  border-bottom: 1px solid #ccc;
  height: 36px;
  line-height: 36px;
  padding-bottom: 1px;
}
.head-left{
  margin-top: 13px;
  padding-bottom: 8px;
  border-bottom: 3px solid #333;
}
.content{
  position: relative;
  padding: 20px;
  border-bottom: 1px solid #ccc;
}
.result{
  position: absolute;
  top: 5px;
  right: 0;
}
.content li{
  line-height: 40px;
}
.content .left{
  width: 100px;
}
.foot{
  padding-top: 5px;
}
</style>



