<template>
  <div class="task">
    <div class="head clearfix">
      <div class="pull-left head-left">任务列表</div>
      <div class="pull-right head-right">
        <div>任务总数：{{job_instance_num}}</div>
        <div>运行中任务数:{{job_instance_running}}</div>
        <div>
          <el-input placeholder="搜索任务名称" size="small">
            <template slot="append"><el-button icon="el-icon-search"></el-button></template>
          </el-input>
        </div>
      </div>
    </div>
    <el-table
      :data="tableData">
      <el-table-column
        prop="job_name"
        label="任务名称"
        show-overflow-tooltip
        sortable>
      </el-table-column>
      <el-table-column
        prop="spider_type"
        label="采集形式"
        sortable>
      </el-table-column>
      <el-table-column
        prop="run_time"
        label="任务运行时间"
        sortable>
      </el-table-column>
      <el-table-column
        prop="spider_freq"
        label="采集频次"
        sortable>
      </el-table-column>
      <el-table-column
        prop="run_times"
        label="任务运行次数"
        sortable>
      </el-table-column>
      <el-table-column
        prop="job_status"
        label="任务状态"
        sortable>
      </el-table-column>
      <el-table-column
        label="更改状态"
        width="100">
        <template slot-scope="scope">
          <el-button size="mini"  @click="changeStatus(scope.row.job_id)" v-show="scope.row.enabled === 0">暂停</el-button>
          <el-button size="mini"  @click="changeStatus(scope.row.job_id)" v-show="scope.row.enabled === -1">开启</el-button>
          <el-button size="mini"  @click="changeStatus(scope.row.job_id)" v-show="scope.row.enabled === 1" :disabled="scope.row.enabled === 1">完成</el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        width="150">
        <template slot-scope="scope">
          <el-button size="small" type="text" @click="$router.push('/task/detail/' + scope.row.job_id)">查看</el-button>
          <el-button size="small" type="text" @click="$router.push('/task/edit/' + scope.row.job_id)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from '@/config/http'
import qs from 'qs'
export default {
  name: 'task',
  data () {
    return {
      tableData: [],
      job_instance_num: 0,
      job_instance_running: 0
    }
  },
  methods: {
    fetchData () {
      axios.get('/api/joblist').then(res => {
        if (res.data.code === 200) {
          this.tableData = res.data.rst
          this.job_instance_num = res.data.job_instance_num
          this.job_instance_running = res.data.job_instance_running
        }
      })
    },
    changeStatus (id) {
      axios.put('/api/joblist', qs.stringify({job_id: id})).then(res => {
        this.$message.success('更改成果')
        this.fetchData()
      })
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>

<style scoped>
.head{
  border-bottom: 1px solid #ccc;
  height: 34px;
  line-height: 34px;
  padding-bottom: 1px;
}
.head-left{
  border-bottom: 3px solid #333;
}
.head-right>div{
  display: inline-block;
  margin-left: 10px;
}
</style>



